<template>
		<div>
				<div class="item">
						<label>:{{nums}}</label>
						<input type="text" name="" v-model="nums"/>
				</div>
				<div class="item">
						<label>:{{names}}</label>
						<input type="text" name="" v-model="names"/>
				</div>
				<div class="item">
						<div>
							<label>:</label>
							<span>{{sex==0?"":""}}</span>
						</div>
						<div>
							<label></label><input type="radio" name="sex" v-model="sex" value="0">
							<label></label><input type="radio" name="sex" v-model="sex" value="1">
						</div>
				</div>
				<div class="item">
						<div>
								<label>:</label>
								<span>{{ age }}</span>	
						</div>
						<div>
							<select v-model="age">
							  <option>20</option>
							  <option>20-30</option>
							  <option>30-40</option>
							</select>
						</div>
				</div>
				<ui-button color="red" class="del" @click="add()"></ui-button>
		</div>      
</template>
<script>
import {UiButton}  from 'keen-ui';
	export default{
		data(){
			return{
				nums:'',
				names:'',
				sex:'',
				age:'',
			};
		},
		methods:{
			 add(){
			 		const student={
			 					nums:this.$data.nums,
					 			names:this.$data.names,
					 			age:this.$data.age,
					 			sex:this.$data.sex,
					}
			 		if(student.names!==''&&student.sex!==''&&student.age!==''){
					 	  this.$store.dispatch('addStudent',student);
					}else{
						alert('');
					}
			 }
		}
	}
</script>


